import '../Assets/Styles/EditListingForm.css';
import Card from "react-bootstrap/Card";
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import { useHistory } from 'react-router-dom';
import React, { useState,useEffect  }from "react";


 function Editlistingform() {
    
    let history = useHistory();
    const [users, setUsers] = useState([]);

    const [loading,setLoading]=useState(true);
    // const [blockchainType, setBlockchainType] = useState(users.blockchainType);

    const redirect = (e) => {
      history.goBack();
      e.preventDefault();
    }

    // const saveData = async()=>{
    //     var id = localStorage.getItem("blockchainId")
    //     const response =await fetch(`http://192.168.0.217:31400/v1/network/${id}`,
    //     method= 'PUT',
    //     body = {

    //     }
    //     )
    //     const data=await response.json();
    //     setUsers(data);
    // }
    // }

    const fetchById =async()=>{    
        var id = localStorage.getItem("blockchainId")
        const response =await fetch(`http://192.168.0.217:31400/v1/network/${id}`)
        const data=await response.json();
        setUsers(data);
    }

    useEffect(() => {
        fetchById().then(data=>{
          setLoading(false);
        })
        .catch(error=>{
          setLoading(false)
        })
}, []);


return (
        <div className="content">
            <nav className="navbar navbar-expand-lg navbar-light bg-light contentcolor">
                <div className="container-fluid">
                    <div className="heading">
                        <h4>Blockchain Networks</h4>
                    </div>
                </div>
            </nav>

            <div className="row">
                    <div className="breadcrumb">
                    <ul>
                    <li>Blockchain Network/ Blockchain Listing/ <b>Edit Form</b></li>
                    </ul> 
                    </div>
            </div>

            <div className="row1">
                <Card className="card-form" >
                    <Card.Body>
                        <div className="heading-icon">
                            <h4><span><img src="../images/edit-form-icon.png"/></span>Edit Form</h4>
                        </div>

                        <form className='formedit'>
                            <Row className="row-bottom-top">
                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Blockchain Type</label>
                                        </div>
                                        <div md={8}>
                                        <input type="text" value={users.blockchainType} className="network wd" placeholder="Enter name" />

                                        </div>
                                    </Row>
                                </Col>

                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Network Name</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" value={users.networkName} className="network wd" placeholder="Enter network name" />
                                        </div>
                                    </Row>
                                </Col>
                            </Row>

                            <Row className="row-bottom">
                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Number Of Nodes</label>
                                        </div>
                                        <div md={8}>
                                        <input type="text" value={users.nodes} className="nodes wd" placeholder="Enter name" />

                                        </div>
                                    </Row>
                                </Col>

                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Number Of Members</label>
                                        </div>
                                        <div md={8}>
                                        <input type="text" value={users.members} className="member wd" placeholder="Enter name" />

                                        </div>
                                    </Row>
                                </Col>
                            </Row>

                            <Row className="row-bottom">
                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Channel Name</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" value={users.networkName} className="channel wd" placeholder="Enter channel name" />
                                        </div>
                                    </Row>
                                </Col>

                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Owner</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="owner wd" placeholder="Enter owner" />
                                        </div>
                                    </Row>
                                </Col>
                            </Row>

                            <Row className="row-bottom">
                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Number of peers</label>
                                        </div>
                                        <div md={8}>
                                        <input type="text" value={users.peers} className="peers wd" placeholder="Enter name" />

                                        </div>
                                    </Row>
                                </Col>

                                {/* <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Upload</label>
                                        </div>
                                        <div md={8}>
                                            <input type="file" className="filealign wd" placeholder="Network Name" />
                                        </div>
                                    </Row>
                                </Col> */}
                            </Row>

                            <Row>
                                <div className="btn-btm">
                                    <button onClick= {redirect} className="nav-save">Back</button>
                                    <button  className="nav-cancel">Save</button>
                                    <button className="nav-cancel">Cancel</button>
                                </div>
                            </Row>
                        </form>
                    </Card.Body>
                </Card>
            </div>

        </div>

    );
}

export default Editlistingform;